.footer {
    background-color: #333;
    width: 100%;
    text-align: left;
    font-family: sans-serif;
    font-weight: bold;
    font-size: 16px;
    padding: 40px 10px 20px 10px;
  }
  
  .footer .footer-left,
  .footer .footer-center,
  .footer .footer-right {
    display: inline-block;
    vertical-align: top;
  }
  
  
  /* footer left*/
  
  .footer .footer-left {
    min-width: 33%;
    max-width: 100%;
    margin: auto;
  }
  
  .footer .about {
    line-height: 20px;
    color: #ffffff;
    font-size: 13px;
    font-weight: normal;
    margin: 0;
  }
  
  .footer .about span {
    display: block;
    color: #ffffff;
    font-size: 14px;
    font-weight: bold;
    margin-bottom: 20px;
  }
  
  .footer .icons {
    padding: 2% ;
    min-height: 100px;
    border-radius: 20px;
    margin: 20px;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    min-width: 31%;
    max-width: 100%;
  }
  
  .icons a {
    display: inline-block;
    width: 45px;
    height: 35px;
    cursor: pointer;
    border-radius: 2px;
    font-size: 25px;
    color: #ffffff;
    text-align: center;
    line-height: 35px;
    margin-right: 3px;
    margin-bottom: 5px;
    min-width: 20%;
    max-width: 80%;
  }
  
  
  /* footer center*/
  
  .footer .footer-center {
    width: 30%;
  }
  
  .footer .footer-center i {
    background-color: #33383b;
    color: #ffffff;
    font-size: 25px;
    width: 38px;
    height: 38px;
    border-radius: 50%;
    text-align: center;
    line-height: 42px;
    margin: 10px 15px;
    vertical-align: middle;
  }

  .footer .footer-center i:hover {
    background-color: var(--theme-color);
  }
  
  .footer .footer-center i.fa-envelope {
    font-size: 17px;
    line-height: 38px;
  }
  
  .footer .footer-center p {
    display: inline-block;
    color: #ffffff;
    vertical-align: middle;
    font-size: 13px;
    margin: 0;
  }
  
  .footer .footer-center p span {
    display: block;
    font-weight: normal;
    font-size: 14px;
    line-height: 2;
  }
  
  .footer .footer-center p a {
    color:white;
    text-decoration: none;
  }
  
  
  /* footer right*/
  
  .footer .footer-right {
    min-width: 35%;
  }
  
  .footer h2 {
    text-align: center;
    color: #ffffff;
    font-size: 36px;
    font-weight: normal;
    padding-top: 35px;

  }
  
  .footer h2 span {
    color: var(--theme-color);
  }
  
  .footer .menu {
    color: #ffffff;
    margin: 20px 0 12px;
    padding: 0;
  }
  
  .footer .menu a {
    display: inline-block;
    line-height: 1.8;
    text-decoration: none;
    color: inherit;
  }
  
  .footer .menu a:hover {
    color: var(--theme-color);
  }
  
  .footer .name {
    color: white;
    font-size: 14px;
    font-weight: normal;
    margin: 0;
  }
  
  @media (max-width: 860px) {
    .footer {
      font-size: 14px;
    }
    .footer .footer-left,
    .footer .footer-center,
    .footer .footer-right {
      display: block;
      width: 100%;
      margin-bottom: 40px;
      text-align: center;
      
    }
    .footer .footer-center i {
      margin-left: 0;
    }
  }

  .media {
      position: absolute;
      top: 40%;
      left: 50%;
      transform: translate(-53%, -50%);
  }


   .icon {
    display: inline-block;
  vertical-align: middle;
  transform: perspective(1px) translateZ(0);
  box-shadow: 0 0 1px rgba(0, 0, 0, 0);
  overflow: hidden;
  transition-duration: 0.5s;
  transition-property: color, background-color;
  }

  .icon:hover, .icon:focus, .icon:active {
    -webkit-animation-name: hvr-back-pulse;
    animation-name: hvr-back-pulse;
    -webkit-animation-duration: 1s;
    animation-duration: 1s;
    -webkit-animation-delay: 0.5s;
    animation-delay: 0.5s;
    -webkit-animation-timing-function: linear;
    animation-timing-function: linear;
    -webkit-animation-iteration-count: infinite;
    animation-iteration-count: infinite;
    background-color: var(--theme-color);
    color: white;
  } 

  .Contact {
    margin: 10px;
    display: center;
    align-content: center;
    flex-direction: row;
    align-items: center;
    min-width: 30%;
    max-width: 90%;
  }



.navbar-header {
    display: flex;
    background-color: #333;
    justify-content: space-between;
    align-items: center;
    padding: 5px 5%;
  }
  
.navbar-ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    background-color: #333;
}
  
.navbar-li {
    font-family: "Montserrat", sans-serif;
    font-weight: 500;
    font-size: 16px;
    color: #ecf0f1;
    float: left;
}
  
.navbar-li a {
    display: block;
    color: white;
    text-align: center;
    padding: 24px 16px;
    text-decoration: none;
    border-radius: 10px;
}
  
  /* Change the link color to #111 (black) on hover */
  
.navbar-li a:hover {
    border-radius: 20px;
    transition: 1s;
    background-color: var(--theme-color);
}

  
.logo {
    flex-shrink: 0;
    max-height: 10vh;
}

.open-menu-btn {
    float: right;
    color: white;
    font-size: 35px; 
    display: none;
    cursor: pointer;
}

.close-btn {
    display: none;
    font-size: 50px;
    cursor: pointer;
    color: white;
}

.close-btn:hover, .open-menu-btn:hover {
    transition: 0.2s;
    color: var(--theme-color);
}

#chk, #chk2, #chk3, #chk4 {
    position: absolute;
    visibility: hidden;
}

@media screen and (max-width: 550px) {
    .close-btn {
        display: block;
        position: absolute;
        top: 40px;
        right: 40px;
    }

    .open-menu-btn{
        display: block;
    }

    .navbar-ul {
        position: fixed;
        width: 100%;
        height: 100vh;
        left: 100vh;
        right: 0;
        top: 0;
        padding: 80px;
        line-height: normal;
        transition: 0.7s;
    }
    .navbar-li {
      padding: 10px;  
      float: none;
      display: block;
    }

    #chk:checked ~ .navbar-ul {
        left: 0vh;
    }
    #chk2:checked ~ .navbar-ul {
        left: 100vh;
    }
    #chk3:checked ~ .navbar-ul {
        left: 100vh;
    }
    #chk4:checked ~ .navbar-ul {
        left: 100vh;
    }
    

  }

/* CSS for Recipe site and recipe components
*/
.recipe-App {
    padding-top: 20px;
    background-color: var(--theme-color);
}

.search-form{
    min-height: 15vh;
    display: flex;
    justify-content: center;
    align-items: center;
    
}

input[type=text], select, textarea {
    width: 70%; /* Full width */
    margin-left: 10px;
    padding: 12px; /* Some padding */ 
    border: 1px solid #ccc; /* Gray border */
    border-radius: 6px; /* Rounded borders */
    box-sizing: border-box; /* Make sure that padding and width stays in place */
    margin-top: 20px; /* Add a top margin */
    margin-bottom: 20px; /* Bottom margin */
    resize: vertical /* Allow the user to vertically resize the textarea (not horizontally) */
  }


.search-button {
    background: #333;
    border: none;
    padding: 13px 20px;
    color: white; 
    border-radius: 6px;
    cursor: pointer;
    margin:10px;
}

.search-button:hover {
    transition: 0.4s;
    background-color: rgb(97, 189, 35);

   
    
}


/*Recipe Components
*/

.recipe-header {
    font-family: "Montserrat", sans-serif;
    font-weight: 500;
    font-size: 22px;
    padding: 10px 10%;
    text-align: center;
    color: whitesmoke;

}

.recipe-title {
    display: flex;
    color: #333;
    justify-content: space-between;
    align-items: center;
    padding: 10px 10%;
}

.recipe-ingredient {
    list-style-type: circle;
    color: #333;
}

.recipe-img {
    border-radius: 50%;
    width: 200px;
    height: 200px;

}

.recipes {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
}

.recipe-box {
    background-color: whitesmoke;
    padding: 7% ;
    min-height: 300px;
    border-radius: 10px;
    box-shadow: 0px 5px 20px rgb(70,70,70);
    margin: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    min-width: 31%;
    max-width: 80%;
}
