@import "./components/Footer.css";
@import "./components/NavBar.css";
@import "./pages/Recipe.css";
@import "./index.css";



/* Global Variables */


:root {
  --theme-color: rgb(69, 162, 158);
}

html {
  scroll-behavior: smooth;
}


.app {
display: flex;
justify-content: space-around;
align-items: center;
}

.red{
  color: red;
}

.button {
  background-color: rgb(161, 122, 70);
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
}

h2 {
  font-family: "Montserrat", sans-serif;
  font-weight: 500;
  font-size: 22px;
  padding: 10px 10%;
  text-align: center;
  color: whitesmoke;
}

p {
  font-family: "Montserrat", sans-serif;
  font-weight: 500;
  font-size: 16px;
  padding: 10px 10%;
  text-align: center;
  color: whitesmoke;
}

label {
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  font-size: 19px;
  font-weight: 400;
  text-align: center;
  color: white;
}
